<template>
	<view class='wrapper'>
		<view class="persion_info padding">
			<view class='desc flex-row-between-start'>
				<view class="info">
					<image src="../../static/default_avatar.png" class="avatar"></image>
					<view class='other'>
						<view class='name_w'>
							<text class="name">{{orderInfo.memberName}}</text>
							<text class="status unpass">{{statusText}}</text>
						</view>
						<view class='tel' @click="tel(orderInfo.memberMobile)">{{orderInfo.memberMobile}}</view>
					</view>
				</view>
				<view class="btn" 
					v-if="orderInfo.orderList[0].orderState!=-1 && orderInfo.orderList[0].orderState<15" 
					@click="cancleOrder"
				>
					<button type="default" class="cu-btn">取消订单</button>
				</view>
			</view>
			<view class="orderList">
				<view class='item text-color'>
					<view class="key">意向车型</view>
					<view class="value">{{orderInfo.orderList[0].carTypeName}}</view>
				</view>
				<view class='item text-color'>
					<view class="key">意向4S店</view>
					<view class="value">{{orderInfo.orderList[0].store4sName}}</view>
				</view>
				<view class='item text-color'>
					<view class="key">询价情况</view>
					<view class="value">{{orderInfo.orderList[0].inquiryInfo!=null?orderInfo.orderList[0].inquiryInfo+' 万元':''}}</view>
				</view>
				<view class='item text-color'>
					<view class="key">购车进度</view>
					<view class="value">{{orderInfo.orderList[0].nodeStepName}}</view>
				</view>
				<view class='item text-blue'>
					<view class="key">推荐员工</view>
					<view class="value" @click="telPhone(orderInfo.staffMobile)">{{orderInfo.staffName}}({{orderInfo.staffMobile}})</view>
				</view>
			</view>
		</view>
		<view class="time_line">
			<view class="cu-timeline">
				<view 
					:class="['cu-item',currentStep>=item.step?'text-golden':'no_pass',currentStep==item.step && canNextStep?'active':'']"
					v-for="(item,idx) in progressList"
					:key="idx"
				>
					<block v-if="item.step==1">
						<view class="look_car time_item">
							<view class="title">{{item.name}}</view>
							<view class='date' v-if='currentStep==item.step && canNextStep' @click="makeSure(item)">
								确认下发
							</view>
							<view v-else class="select_date">{{item.nodeDate!=-1?item.nodeDate:''}}</view>
						</view>
					</block>
					
					<block v-else>
						<view :class="['cu-item',currentStep>=item.step && canNextStep?'text-golden':'no_pass',currentStep==item.step && canNextStep?'active':'']">
							<view class="look_car time_item">
								<view class="title">{{item.name}}</view>
								<view class="affix_list" v-if='currentStep==item.step && canNextStep' >
									<view class="item" v-for="(lett,idx) in affixList" :key="idx" @click='lookAffix(lett)'>
										<image :src="lett" class='item'></image>
									</view>
								</view>
								<!-- 项目组管理员确实是否发送奖励 判断是否为大客户-->
								<view class="check_box flex-row-start" v-if="currentStep>=item.step && item.code==20 && orderInfo.isVol==0">
									<view class="name">是否发送奖励？<text class='text-red'>*</text></view>
									<view class="radio_group ">
										<view class="radio_item" @click="swithRadio(1)">
											<image src="../../static/select.png" v-if="isSend==1"></image>
											<image src="../../static/unselect.png" v-else></image>
											<text>是</text>
										</view>
										<view class="radio_item" @click="swithRadio(0)">
											<image src="../../static/select.png" v-if="isSend==0"></image>
											<image src="../../static/unselect.png" v-else></image>
											<text>否</text>
										</view>
									</view>
								</view>
								
								<view class='date' v-if='currentStep==item.step && canNextStep' @click="makeSure(item)">
									确认审核
								</view>
								<view v-else class="select_date">{{item.nodeDate!=-1?item.nodeDate:''}}</view>
							</view>
						</view>
					</block>
					
				</view>
			</view>
		</view>
		<!-- 图片查看 -->
		<view class="cu-modal" :class="lookImg?'show':''">
			<view class="cu-dialog">
				<view class="bg-img">
					<image :src="currentImgUrl" class="dialogImg" mode="widthFix"></image>
				</view>
				<view class="cu-bar bg-white">
					<view class="action margin-0 flex-sub  solid-left" @click="hideModal">关闭</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import {http2Https,formatTime} from '../../common/tools.js'
	export default {
		data() {
			return {
				orderInfo:{},
				currentStep:1,
				progressList:[],
				affixList:[],
				paramInfo:{
					orderId:'',
					type:'',//处理类型	Integer	是	1-下发，2-党支部审核，3-项目组审核
				},
				canNextStep:true, //是否可以进行下一步
				lookImg:false,
				currentImgUrl:null,
				isSend:1,//0,1 //是否发送奖励
				statusText:''
			};
		},
		methods:{
			resetProgress(){
				let step=1
				this.progressList.forEach((item,index)=>{
					item.step=index+1; // 赋予步数
					item.havePermission=true
					if(item.nodeDate!=-1){
						item.nodeDate=formatTime(item.nodeDate,true)
						step++
					}
				})
				this.currentStep=step
			},
			cancleOrder(){
				uni.showModal({
					title: '提示',
					content: `确认取消该订单吗？`,
					success: (res)=>{
						if (res.confirm) {
							this.request("/order/admin/cancel",{orderId:this.paramInfo.orderId}).then((res)=>{
								if(res.resultCode==0){
									uni.showToast({
										title:res.resultMsg,
										icon:"none",
										success:()=>{
											this.statusText="订单已取消"
											setTimeout(()=>{
												uni.navigateBack({
													delta:1
												})
											},1500)
										}
									})
								}else{
									uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
								}
							})
						}
					}
				})
				
			},
			tel(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			swithRadio(select){
				if(this.currentStep>3){ //
					return
				}
				this.isSend=select
			},
			getPicList(){
				if(this.orderInfo.orderList[0].memberAttUrls){
					let _imgList=this.orderInfo.orderList[0].memberAttUrls.split(',')
					this.affixList=[..._imgList]
				}
				if(this.orderInfo.orderList[0].staffAttUrls){
					let _imgList=this.orderInfo.orderList[0].staffAttUrls.split(',')
					this.affixList=this.affixList.concat(_imgList)
				}
			},
			lookAffix(currentUrl){
				if(this.mobileModel=="VOG-AL00"){ //华为p30
					this.currentImgUrl=currentUrl;
					this.lookImg=true
				}else{
					uni.showLoading()
					uni.previewImage({
						current:currentUrl,
						urls:this.affixList,
						longPressActions:{
							itemList: ['发送给朋友', '保存图片', '收藏']
						},
						fail() {
							this.currentImgUrl=currentUrl;
							this.lookImg=true
						},
						complete(){
							uni.hideLoading()
						}
					})
				}
			},
			hideModal(){
				this.lookImg=false
			},
			telPhone(tel){
				uni.showModal({
					title:"提示",
					content:"是否确认拨打电话",
					success:(res)=>{
						if(res.confirm){
							uni.makePhoneCall({
							  phoneNumber:tel //仅为示例，并非真实的电话号码
							})
						}
					}
				})
			},
			makeSure(item){
				this.paramInfo.type=item.step
				var _content="";
				if(item.step==1){ //下发订单
					_content="是否确认下发该订单？"
				}else{
					if(item.step==3){ // 项目组
						if(this.orderInfo.isVol==1){ //如果是大客户不发送奖励
							this.isSend=0
						}
						this.paramInfo.awardFlag=this.isSend
						if(this.paramInfo.awardFlag==null){
							uni.showToast({title:'请选择是否发放奖励',icon:'none'})
							return
						}
						_content=`是否确认审核该订单\n并${this.isSend==1?'发放':'不发放'}奖励`
					}else{ // 党支部管理员
						_content="是否确认审核该订单？"
					}
				}
				uni.showModal({
					title: '提示',
					content: _content,
					success: (res)=>{
						if (res.confirm) {
							this.request("/order/admin/treat",this.paramInfo).then((res)=>{
								if(res.resultCode==0){
									item.nodeDate=formatTime(res.data.opTime,true)
									// 修改文案
									switch(item.step){
										case 1 :this.statusText="订单已下发";break
										case 2 :this.statusText="党支部已审核";break
										case 3 :this.statusText="项目组已审核";break
									}
									
									if(item.step==1){ //如果党支部下发订单之后，置成 党支部待审核 10隐藏取消订单按钮,维护订单状态
										this.orderInfo.orderList[0].orderState=10
									}
									
									if(item.step==2){ //如果党支部审核过之后，置成 项目组待审核 15隐藏取消订单按钮,维护订单状态
										this.orderInfo.orderList[0].orderState=15
									}
									
									if(res.data.continueTreat){ //判断是否可以继续操作
											this.currentStep++
									}
									if(item.step==3){ // 因为 第三部的时候 continueTreat这个参数肯定是false
										this.currentStep++
									}
									this.canNextStep=res.data.continueTreat
								}else{
									uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
								}
							})
						}
					}
				})
			}
		},
		onLoad(params){
			this.orderInfo=JSON.parse(params.orderInfo)
			// console.log(this.orderInfo)
			this.progressList=this.orderInfo.orderList[0].adminNodes
			this.paramInfo.orderId=this.orderInfo.orderList[0].orderId
			this.statusText=this.orderInfo.orderList[0].orderStateName
			// 如果有附件
			this.getPicList()
			this.resetProgress()
		}
	}
</script>

<style lang="stylus">
	.wrapper
		padding-bottom:50upx
		.persion_info
			.desc
				.info
					display:flex
					flex-direction row
					align-items center
					.avatar
						width:108upx
						height:108upx
						border-radius 50%
						overflow hidden
						margin-right:40upx
					.other
						.name_w
							display:flex
							flex-direction row
							align-items center
							margin-bottom:15upx
							.name
								font-size:32upx;
								font-family:PingFangSC-Semibold,PingFang SC;
								font-weight:600;
								color:rgba(30,30,30,1);
								margin-right:20upx
							.status
								height:30upx;
								line-height 30upx
								text-align center
								border-radius:4upx;
								padding:2upx 10upx
								font-size:24upx
								color:#fff
								&.unpass
									background:#E17969;
								&.pass
									background:#70D097
				.btn
					button
						background:rgba(31,35,52,1);
						border-radius:8upx;
						color:#fff
			.orderList
				margin-left:143upx
				margin-top:30upx
				.item
					display:flex
					flex-direction row
					margin-top:30upx
					align-items flex-start
					font-size:24upx;
					font-family:PingFangSC-Regular,PingFang SC;
					font-weight:400;
					.text-color
						color:rgba(30,30,30,1);
					.key
						width:30%
					.value
						width:70%
		.cu-timeline
			.cu-item
				.time_item
					padding-top:12upx
					.title
						font-size:28upx;
						font-family:PingFangSC-Regular,PingFang SC;
						font-weight:400;
						color:rgba(178,182,192,1);
					.affix_list
						display:flex
						margin-top:30upx
						flex-direction row
						align-items center
						justify-content flex-start
						.item
							width:120upx
							height:120upx
							margin-right:40upx
					.check_box
						font-size:24upx;
						font-family:PingFangSC-Medium,PingFang SC;
						color:rgba(30,30,30,1);
						font-weight:bold
					 .radio_group
						 justify-content flex-start
						 .radio_item
							height:100upx
							text
								width:auto
								font-weight:bold
								font-size:24upx
					.date
						height:88upx;
						line-height 88upx
						background:rgba(248,249,252,1);
						border-radius:8upx;
						font-size:28upx;
						font-family:PingFangSC-Medium,PingFang SC;
						font-weight:bold;
						color:#D89864
						text-align center
						margin-top:30upx
					.select_date
						font-size:24upx;
						font-family:PingFangSC-Medium,PingFang SC;
						font-weight:bold;
						color:rgba(30,30,30,1);
						margin-top:20upx
				&.text-golden
					.title
						font-size:28upx;
						font-family:PingFangSC-Regular,PingFang SC;
						font-weight:400;
						color:#D89864
				&.text-golden.active
						.title
							font-size:36upx;
							font-weight:600;
							margin-top:-10upx
</style>